header {
  position: sticky;
  min-height: 50px;
  top: 0;
  /* background-color: #606470; */
  /* background-color: #f7f7f7; */
  background-color: #e6e6e6;
  display: flex;
  flex-flow: row;
  justify-content: flex-end;
  align-items: center;
}

.fim_uhk_logo {
  position: absolute;
  left: 0;
  padding: 0.5rem;
  max-width: 20rem;
}

.mobile_main_nav {
  position: fixed;
  right: 0.5rem;
  top: 0.5rem;
  background-color: #cfcfcf;
  border-radius: 5px;
  z-index: 20;
  transition: 200ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.hide-mobile-nav {
  transform: translateX(calc(+120%));
}

.mobile_main_nav__links {
  list-style: none;
  list-style: none;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}

.mobile_main_nav__link {
  padding: 1rem 2rem;
  font-weight: 500;
  cursor: pointer;
  background-color: #d6d6d6;
  color: #3a3e4d;
}

.mobile_main_nav__link:not(:last-child) {
  border-bottom: 2px solid #009fdf;
}

.hamburger {
  padding-top: 0.5rem;
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
}

.hamburger_strip {
  width: 2rem;
  height: 0.2rem;
  margin-bottom: 0.5rem;
  background-color: #009fdf;
}

.main_nav {
  display: none;
}

.main_nav__links {
  list-style: none;
  display: flex;
  flex-direction: row;
}

.main_nav__link {
  padding: 0.5rem 1rem;
  margin: 0.4rem 1rem;
  border-radius: 5px;
  font-weight: 500;
  cursor: pointer;
  /* background-color: #323643; */
  background-color: #d6d6d6;
  /* color: #f7f7f7; */
  color: #3a3e4d;
  transition: all 100ms;
}

.main_nav__link:hover {
  /* color: #93DEFF; */
  /* background-color: #3a3e4d; */
  background-color: #cccccc;
}

.active_link {
  color: #009fdf;
}
